<template>
  <div class="container">
    <el-form
      :rules="rules"
      class="form-conntainer"
      ref="form"
      :model="form"
      label-width="100px"
    >
      <el-form-item label="项目名称" prop="projectId">
        <el-select
          v-model="form.projectId"
          placeholder="请选择项目名称"
          style="width: 100%;"
          @change="handleChangeProject"
        >
          <el-option
            v-for="attend in projectList"
            :key="attend.value"
            :label="attend.title"
            :value="attend.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="项目阶段" prop="stageId">
        <el-select
          v-model="form.stageId"
          placeholder="请选择项目阶段"
          style="width: 100%;"
        >
          <el-option
            v-for="attend in participateStage"
            :key="attend.value"
            :label="attend.title"
            :value="attend.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="所在地" prop="attendType">
        <el-select
          v-model="form.attendType"
          placeholder="请选择所在地"
          style="width: 100%;"
        >
          <el-option
            v-for="attend in attendTypeList"
            :key="attend.value"
            :label="attend.title"
            :value="attend.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="报工日期">
        <el-date-picker
          type="dates"
          placeholder="选择时间（可多选）"
          v-model="reportDateArr"
          style="width: 100%;"
          format="yyyy-MM-dd"
          value-format="yyyy-MM-dd"
        ></el-date-picker>
      </el-form-item>

      <el-form-item label="报工类型" prop="reportType">
        <el-select
          v-model="form.reportType"
          placeholder="请选择活动区域"
          style="width: 100%;"
        >
          <el-option
            v-for="attend in reportTypeList"
            :key="attend.value"
            :label="attend.title"
            :value="attend.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="日志" prop="reportDetail">
        <el-input type="textarea" v-model="form.reportDetail"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          style="width: 100%;"
          type="primary"
          @click="onSubmit('form')"
          :loading="isLoading"
          >{{ buttonText }}</el-button
        >
      </el-form-item>
    </el-form>
    <el-dialog
      v-if="dialogTableVisible"
      title="填写token"
      :close-on-click-modal="false"
      :visible.sync="dialogTableVisible"
    >
      <el-input
        class="dialog-textarea"
        type="textarea"
        v-model.trim="token"
      ></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogTableVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleInputToken">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { get, post } from "../request";
const texts = [
  "满满的正能量",
  "早晨起来，拥抱太阳",
  "让身体充满，灿烂的阳光",
  "满满的正能量",
  "嘴角向下，会迷失方向",
  "嘴角向上，蒸蒸日上",
  "世上没有路，都是人开创",
  "脚底板磨破了，道路就顺畅",
  "满满的正能量"
];
let currentTextIndex = 0;
export default {
  data() {
    return {
      timer: null,
      isLoading: false,
      dialogTableVisible: true,
      token: "",
      currentTextIndex: 0,
      buttonText: "满满的正能量",
      // type: "1",
      reportDateArr: [],
      form: {
        projectId: "",
        stageId: "",
        attendType: "COMPANY",
        reportDetail: "",
        reportType: "WHOLE_DAY"
      },
      attendTypeList: [],
      reportTypeList: [],
      projectList: [],
      participateStage: [],
      rules: {
        projectId: [
          { required: true, message: "请选择项目名称", trigger: "blur" }
        ],
        stageId: [
          { required: true, message: "请选择项目阶段", trigger: "blur" }
        ],
        attendType: [
          { required: true, message: "请选择所在地", trigger: "blur" }
        ],
        reportType: [
          { required: true, message: "请选择报工类型", trigger: "blur" }
        ],
        reportDetail: [
          {
            required: true,
            min: 10,
            message: "请填写日志（长度大于10字）",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    this.getAttendType();
    this.getReportType();
    setInterval(() => {
      this.getButtonText();
    }, 3000);
  },
  methods: {
    onSubmit(formData) {
      this.$refs[formData].validate(valid => {
        if (valid) {
          this.isLoading = true;
          let reportDateArr = this.reportDateArr;
          let i = 0;
          this.timer = setInterval(() => {
            if (reportDateArr.length === i) {
              clearInterval(this.timer);
              return;
            }
            this.submitFun(reportDateArr[i]);
            i++;
          }, 2000);
          setTimeout(() => {
            this.isLoading = false;
          }, 3000);
        }
      });
    },
    submitFun(reportDate) {
      let params = {
        reportDate,
        ...this.form
      };
      post("/dailyReport/add", params).then(res => {
        this.$message.success(params.reportDate + " 提交成功");
      });
    },

    getButtonText() {
      this.buttonText = texts[currentTextIndex];
      if (currentTextIndex >= texts.length - 1) {
        currentTextIndex = 0;
      } else {
        currentTextIndex = currentTextIndex + 1;
      }
      console.log(this.buttonText, currentTextIndex);
    },
    /**
     * 生成指定范围内随机数
     */
    randomNumBoth(Min, Max) {
      var Range = Max - Min;
      var Rand = Math.random();
      var num = Min + Math.round(Rand * Range); //四舍五入
      return num;
    },
    handleInputToken() {
      this.token && sessionStorage.setItem("token", this.token);
      this.dialogTableVisible = false;
      this.getProjectList();
    },
    handleChangeProject(projectIds) {
      this.getParticipateStage([projectIds]);
    },
    getAttendType() {
      get("/meta/dict/attend-type").then(res => {
        this.attendTypeList = res.data;
      });
    },
    getReportType() {
      get("/meta/dict/report-type").then(res => {
        this.reportTypeList = res.data;
      });
    },
    getProjectList() {
      get("/project/getParticipateProject").then(res => {
        this.projectList = res.data;
      });
    },
    getParticipateStage(projectIds) {
      post("/stage/getParticipateStage", { projectIds }).then(res => {
        this.participateStage = res.data;
      });
    }
  }
};
</script>
<style>
.container {
  padding-top: 10vh;
}
.form-conntainer {
  width: 60%;
  margin: 0 auto;
}
</style>

<style scoped>
>>> .el-form-item__label {
  color: #fff;
}
>>> .el-input__inner {
  color: #fff;
  background: transparent;
}
>>> .el-textarea .el-textarea__inner {
  background: transparent;
  resize: none;
  color: #fff;
}
>>> .dialog-textarea .el-textarea__inner {
  color: #000;
}
>>> .v-modal {
  z-index: 100;
}
</style>



