<template>
  <div id="app">
    <WorkReport class="work-report" />
    <!-- iframe引入的文件必须放在static目录下 引入路径需要使用绝对路径 -->
    <iframe :src="url" class="iframe" />
  </div>
</template>

<script>
import WorkReport from "./components/WorkReport";
export default {
  name: "App",
  data() {
    return {
      url: "./static/lzxg.html"
    };
  },
  components: {
    WorkReport
  }
};
</script>

<style>
.iframe {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  width: 100%;
  border: none;
}
.work-report {
  position: relative;
  z-index: 2001;
}
</style>



